module.exports = "import { Component } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'nz-demo-tabs-disabled',\r\n  template: `\r\n    <nz-tabset>\r\n      <nz-tab\r\n        *ngFor=\"let tab of tabs\"\r\n        [nzTitle]=\"tab.name\"\r\n        [nzDisabled]=\"tab.disabled\">\r\n        {{ tab.name }}\r\n      </nz-tab>\r\n    </nz-tabset>`\r\n})\r\nexport class NzDemoTabsDisabledComponent {\r\n  tabs = [\r\n    {\r\n      name    : 'Tab 1',\r\n      disabled: false\r\n    },\r\n    {\r\n      name    : 'Tab 2',\r\n      disabled: true\r\n    },\r\n    {\r\n      name    : 'Tab 3',\r\n      disabled: false\r\n    }\r\n  ];\r\n}\r\n"